import React, { useState, useEffect } from 'react';
import classes from './quest.module.css';
import bg from './1.png'
import Game from '../Game';
const Quest1 = (props) => {
    const [accepted, setAccepted] = useState(false);
    const dialogues = [
        { line: 'Warrior of Love... Hear... Feel... Think...', complete: false },
        { line: 'Like the rain from a cloudless sky, a gift from thy beloved will be yours. Holding it is nigh!', complete: false },
        { line: 'Complete these quests and thy shall be rewarded. Assay to fail and thy will rend mine heart assunder.', complete: true }
      ];
    return (
        <div>
            {accepted ? (<div>
                <Game startMusic={true} dialogues={dialogues}/>
            </div>)
                :
                <div className={classes.Container}>
                    <span onClick={() => setAccepted(true)}><p className={classes.Name}>Accept</p></span>
                    <p className={classes.Dialogue}>Decline</p>
                    <img src={bg} />
                </div>
            }
        </div>

    )
}
export default Quest1